.container {
    width: 65px;
    height: 65px;
    border: 1px solid black;
    position: relative;
    box-sizing: border-box;
    cursor: pointer;
}

.selected {
    background-color: #1ff859;
}

.banned {
    background-color: #dc3545;
}

.name-block {
    position: absolute;
    bottom: 0;
    width: 100%;
    background: #000000aa;
    color: white;
    font-size: 10px;
}

.banned-foreground {
    width: 100%;
    height: 100%;
    position: absolute;
}

.banned-block {
    width: 65px;
    height: 10px;
    background-color: black;
    position: absolute;
    top: 25px;
}

.left {
    transform: rotate(45deg);
}
.right {
    transform: rotate(-45deg);
}