.sidebar-character {
    border: 1px solid black;
    position: relative;
    box-sizing: border-box;
    height: 156px;
}

.sidebar-character .name-block {
    font-size: 17px;
}

@media only screen and (max-width: 750px) {
    .sidebar-container {
        flex-wrap: wrap;
        display: flex;
        justify-content: center;
        margin: 20px 0;
    }
}