.character-grid {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    width: calc(65px * 6);
}

.generate {
    height: 25px;
    background-color: #3f51b5;
    border: none;
    color: white;
    cursor: pointer;
    border-radius: 4px;
    box-shadow: 0 3px 1px -2px rgba(0,0,0,.2), 0 2px 2px 0 rgba(0,0,0,.14), 0 1px 5px 0 rgba(0,0,0,.12);
}

.action-bar {
    width: calc(65px * 6);
    margin: 0 auto;
    height: 40px;
    display: flex;
    align-items: center;
}

.main-screen {
    justify-content: center;
    display: flex;
    flex-wrap: wrap;
}


@media only screen and (max-width: 750px) {
    .main-screen {
        padding-top: 60px;
    }

    .action-bar { 
        padding: 10px 0;
        position: fixed;
        z-index: 2;
        background: #efefef;
        width: 100%;
        border-bottom: 1px solid black;
        flex-direction: row-reverse;
        box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
    }

    .generate {
        height: 40px;
        margin-right: 15px;
    }
}